import Products from './components/products/Products';
import Search from './components/search/Search';
import Cart from './components/Cart/Cart';
import { stockData } from "./data";
import './App.css';
import React from 'react';
import { setCart, getCart } from "./utils";


class App extends React.Component {

  constructor() {
    super()
    this.state = {
      stockData: stockData,
      searchField: '',
      cartItems: getCart()
    }
  }


  onSearchChange = (event) => {
    this.setState({ searchField: event.target.value })
  }


  addToCart = data => {
    const alreadyInCart = this.state.cartItems.findIndex(
      item => item.id === data.id
    );

    if (alreadyInCart === -1) {
      const updatedItems = this.state.cartItems.concat({
        ...data,
        quantity: 1
      });
      this.setState({ cartItems: updatedItems }, () => setCart(updatedItems));
    } else {
      const updatedItems = [...this.state.cartItems];
      updatedItems[alreadyInCart].quantity += 1;
      this.setState({ cartItems: updatedItems }, () => setCart(updatedItems));
    }
  };


  removeCartQty = data => {
    const alreadyInCart = this.state.cartItems.findIndex(
      item => item.id === data.id
    );
    const updatedItems = [...this.state.cartItems];
    updatedItems[alreadyInCart].quantity -= 1;
    this.setState({ cartItems: updatedItems }, () => setCart(updatedItems));
  }


  deleteItemFromCart = itemToDeleteId => {
    const filteredItems = this.state.cartItems.filter(
      item => item.id !== itemToDeleteId
    );
    this.setState({ cartItems: filteredItems }, () => setCart(filteredItems));
  };




  render() {

    const filteredStock = this.state.stockData.filter(stockData => {
      return (stockData.company.toLowerCase().includes(this.state.searchField.toLowerCase()) ||
        stockData.ticker.toLowerCase().includes(this.state.searchField.toLowerCase()));
    })
    console.log(filteredStock)

    return (
      <div style={{ position: 'relative', marginRight: '300px', minWidth: '600px' }}>
        <Cart deleteItemFromCart={this.deleteItemFromCart} removeCartQty={this.removeCartQty} cartItems={this.state.cartItems} />
        <Search searchChange={this.onSearchChange} />
        <Products addToCart={this.addToCart} stockData={filteredStock} />
      </div>
    );
  }

};

export default App;
