import React from 'react';
import { calculatePrice } from "../../utils";

const Cart = (props) => {


    const cartItems = props.cartItems;


    return (



        <div id='cart' style={{ backgroundColor: 'lightblue', borderRadius: '5%', width: '16rem', position: 'fixed', top: '0', right: '0', display: 'block', margin: '2rem', padding: '1rem' }}>
            <h1>Your cart:</h1>
            <h1>{cartItems.length} items selected</h1>
            <h2>{cartItems.length === 0 && (<p>your cart is empty</p>)}</h2>
            {/* Cart Items */}
            {cartItems.map(item => (
                <div key={item.id} style={{ display: 'flex', alignItems: 'flex-end', padding: '2px', margin: '2px' }}>
                    {item.company} x {item.quantity} - $
                    {(item.quantity * item.stockPrice).toFixed(2)}
                    <button style={{ margin: '0 5px' }} onClick={() => props.deleteItemFromCart(item.id)}>X</button>
                    <button onClick={() => props.removeCartQty(item)}>-</button>
                </div>
            ))}
            {/* Cart total */}
            <h1>Total: {calculatePrice(cartItems)}</h1>
        </div>

    );
}

export default Cart;