import React from 'react';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';


const Products = (props) => {

    const showCart = () => {
        document.getElementById('cart').style.display = 'block'
    }



    return (

        <div className="stock-container" style={{ display: 'flex', flexDirection: 'row', flexWrap: 'wrap' }}>
            {props.stockData.map((data, key) => {
                return (
                    <div key={key}  >
                        <Card style={{ width: '17rem', backgroundColor: 'lightgreen', border: '2px solid white', borderRadius: '10%', padding: '8px', margin: '8px' }}>
                            <Card.Img style={{ width: '6rem', height: '6rem' }} variant="top" src={data.img} />
                            <Card.Body>
                                <Card.Title>{data.company}</Card.Title>
                                <Card.Text>
                                    {data.stockPrice + "$"}
                                </Card.Text>
                                <Button onClick={() => { props.addToCart(data); showCart() }} variant="primary">{data.ticker}</Button>
                            </Card.Body>
                        </Card>
                    </div>
                );
            })}
        </div>

    );
}

export default Products;